.node :not(.not-node-ritch-text) ul {
  @apply ml-4 mb-6;
}
.node :not(.not-node-ritch-text) ul li {
  @apply relative  mb-2;
}
.node :not(.not-node-ritch-text) ul li:before {
  @apply absolute block bg-primary rounded-full;
  content: "";
  top: 0.625rem;
  left: -0.875rem;
  width: 0.3125rem;
  height: 0.3125rem;
}

.maintenance {
  @apply bg-primary text-neutral-white p-6 pt-16 pb-12 md:p-16 md:pt-[100px];
}
.maintenance__content {
  @apply flex flex-col justify-center items-center;
}
.maintenance__settings_ico {
  @apply pb-10;
}
.maintenance__title {
  @apply pb-6 text-center;
}
.maintenance__message {
  @apply text-primary-300 text-center max-w-[680px];
}
.maintenance__logo {
  @apply pt-[150px] md:pt-[180px];
}